import React,{ useContext, useState, useRef } from 'react';
import { Threndcontext } from "../Context/Context";

// import { useHistory } from "react-router-dom";





function Thrend() {
//  const history = useHistory;

  const { thrends, addthrend } = useContext(Threndcontext);

  const getTitleRef = useRef();
  const getContentRef = useRef();

  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");

  const newThrend = {
      title,
      content
    }

  return (
    <div className="thrends">
      <h1>Blog Thread</h1>
      {thrends.map((thrend, index)=>(
        <div key={index} className="thrend_container">
        <p className="thrend_title">{thrend.title}</p>
        <p className="thrend_content">{thrend.content}</p>
        </div>
      ))}
      <form onSubmit={(e)=>addthrend(e, newThrend, getTitleRef, getContentRef)}>
        <input 
        className="title_input"
        type="text" 
        placeholder="Title"
        onChange={(e)=> setTitle(e.target.value)}
        ref={getTitleRef}
        />
        <textarea 
         className="content_input"
         placeholder="Content"
         onChange={(e)=> setContent(e.target.value) }
         ref={getContentRef}>
        </textarea>
        <input 
        type="submit" 
        value="Add thrend"/>
      </form>
    </div>
  )
}

export default Thrend;
