import React, { createContext, useReducer } from "react";
import Appreducer from "./Appreducer";


 const initialstate = {
  thrends:[
    {
      title: "football",
      content:"there is a debate about the greatest player of all time, which includes cristiano ronaldo and lionel messi"
    },
    {
      title: "current afairs",
      content:"nigeria government is the worse government in the world"
    },
  ]
}

export const Threndcontext =  createContext(initialstate);

export const ThrendProvider = (props)=>{

   const [state, dispatch] = useReducer( Appreducer, initialstate );

   const addthrend = (e, newThrend, getTitleRef, getContentRef, )=>{
     e.preventDefault();
     dispatch({type: "ADD_NEW_THREND", payload: newThrend })
     getTitleRef.current.value= "";
     getContentRef.current.value= "";
   //  history.push("/post");

   }

  return(
    <Threndcontext.Provider value={{
      thrends:state.thrends,
      addthrend
    }}>
      { props.children }
    </Threndcontext.Provider>
  )

}